    <div class="main">
      <div class="module kfc">
        <router-link
          tag="div"
          class="console_box"
          v-for="(item, index) in module"
          :key="index"
          @click="$router.push({ name: item.router })"
          :to="{ name: item.router }"
        >
          <div class="content">
            <div class="console-icon">
              <img :src="item.icon" alt="item.name" />
            </div>
            <div class="name">{{ item.name }}</div>
          </div>
          <!-- <div class="bottom">
            查看详情
          </div> -->
        </router-link>
      </div>
