<script>
import HeaderUser from "@/components/headerUser.vue";
export default {
  beforeRouteEnter(to, from, next) {
    let roler = localStorage.getItem("roler");
    if (roler === "b") next("/backend");
    else if (roler === "s" || roler === "sb") next();
    else {
      console.log("没有权限，返回控制中心");
      localStorage.clear();
      window.location.replace(window.MYHOST);
      next(false);
    }
  },
  components: {
    HeaderUser
  },
  data() {
    return {
      boxStyle: {
        height: "20%"
      },
      module: [
        {
          name: "安全事件监控",
          icon: require("@/assets/images/icon_1.png"),
          router: "securityMonitor"
        },
        {
          name: "攻击态势分析",
          icon: require("@/assets/images/icon_2.png"),
          router: "attackAnalysis"
        },
        {
          name: "应用安全分析",
          icon: require("@/assets/images/icon_3.png"),
          router: "appAnalysis"
        }
      ]
    };
  },
  mounted() {
    this.resizeBox();
    window.addEventListener("resize", this.resizeBox, false);
  },
  methods: {
    resizeBox() {
      this.$nextTick(() => {
        let box = document.getElementsByClassName("console_box");
        this.boxStyle.height = box[0].offsetWidth / (300 / 400) + "px";
      });
    }
  },
  destroyed() {
    window.removeEventListener("resize", this.resizeBox, false);
  }
};
</script>
